<template>
  <div class="project-manage-wrapper">
    <app-header title="项目管理" />

    <div class="verify-list">
      <van-tabs v-model="active">
        <van-tab title="已授权">
          <list type="auth" />
        </van-tab>
        <van-tab title="审批中">
          <list type="approve" />
        </van-tab>
      </van-tabs>
    </div>
    <div class="footer-ctrl">
      <app-button @click="toAuth">申请项目权限</app-button>
    </div>
  </div>
</template>

<script>
import { Tab, Tabs } from 'vant';
import List from '@src/widget/project-manage-list';
import AppButton from '@src/components/app-button';
import SsoCenterService from '@services/sso-center-service';
import { getUserContext } from '@yl-app-jssdk/context';
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    List,
    AppButton,
  },

  data() {
    return {
      active: 0,
    };
  },

  methods: {
    toAuth() {
      let user = getUserContext();
      return SsoCenterService.openPage(
        `/auth/role`,
        /** sso页面真实姓名带入且不可以修改 */
        {
          realname: user.realname,
          realnameReadonly: '1',
        }
      );
    },
  },
};
</script>

