30;\n\n@mixin theme($color-theme) {\n  .color {\n    color: $color-theme;\n  }\n  .border-color {\n    border-color: $color-theme;\n    &::after {\n      border-color: $color-theme;\n    }\n  }\n}\n\n.custom-theme {\n  @include theme('#2c5cf7');\n  .gradient-background {\n    background: linear-gradient(\n      90deg,\n      rgba($color-theme, 1) 0%,\n      rgba($color-theme, 0.8) 100%\n    );\n  }\n}\n\n.custom-theme {\n  @include theme(#2c5cf7);\n}\n\n.test-theme {\n  @include theme(#f00);\n}\n","@import \"~@src/styles/variable.scss\";\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import '@src/styles/variable.scss';\n// @import '@styles/mixin/iphoneX.scss';\n\n.project-manage-wrapper {\n  display: flex;\n  flex: 1;\n  width: 100%;\n  height: 100%;\n  flex-direction: column;\n  overflow: hidden;\n\n  // /deep/ .van-tabs__content {\n  // border-top: 10px solid #f7f8fa;\n  // }\n\n  .verify-list {\n    display: flex;\n    flex: 1;\n    overflow: hidden;\n    border-top: 1px solid #f7f8fa;\n    // @include cssIphoneX(bottom, border-box);\n\n    /deep/.van-tabs {\n      display: flex;\n      flex: 1;\n      flex-direction: column;\n      overflow: hidden;\n\n      .van-tab--active {\n        color: $color-theme;\n      }\n\n      .van-tabs__content {\n        display: flex;\n        flex: 1;\n        overflow: hidden;\n\n        .van-tab__pane {\n          display: flex;\n          flex: 1;\n        }\n      }\n\n      .van-tabs__line {\n        width: 16px !important;\n        background-color: $color-theme;\n        font-weight: 500;\n      }\n\n      .van-tabs__nav {\n        font-size: 16px;\n        color: #666666;\n      }\n    }\n  }\n\n  .footer-ctrl {\n    padding: 6px 15px;\n    background: #ffffff;\n    width: 100%;\n    height: 50px;\n    // @include cssIphoneX(bottom, border-box, 6px);\n  }\n}\n"]}]);
