<template>
  <div class="contract-item-wrapper" @click="$emit('click')">
    <div v-for="(item, index) in list" :key="index">
      <div
        v-if="item.childList && item.childList.length"
        class="child-container"
      >
        <app-fold
          :showTitleLine="true"
          :defaultFold="true"
          :title="item.proj_name"
        >
          <div v-for="(child, _index) in item.childList" :key="child.id">
            <div
              class="item-con"
              :class="{ 'bottom-border': _index !== item.childList.length - 1 }"
              @click="toDetail(child)"
            >
              <div class="name">{{ child.proj_name }}</div>
              <van-icon name="info-o" class="info-icon" v-if="showInfo" />
            </div>
          </div>
        </app-fold>
      </div>
      <div
        v-else
        class="item-con no-child-item"
        :class="{ 'bottom-border': index !== list.length - 1 }"
        @click="toDetail(item)"
      >
        <div class="name">{{ item.proj_name }}</div>
        <van-icon name="info-o" class="info-icon" v-if="showInfo" />
      </div>
    </div>
  </div>
</template>

<script>
import AppFold from '@components/app-fold';

export default {
  components: {
    AppFold,
  },
  props: {
    type: {
      type: String,
      default: '',
    },
    list: {
      type: Array,
      default: () => [],
    },

    showInfo: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {};
  },
  methods: {
    toDetail(item) {
      console.log('item ======> ', item);
      if (this.type !== 'approve') return;
      console.log('params ====> ', {
        applyId: item.id,
        projectId: item.object_id,
      });
      this.$router.push({
        name: 'applyDetail',
        params: { applyId: item.apply_user_id, projectId: item.object_id },
      });
      // this.$router.push({
      //   path: '/user/apply-detail',
      //   params: {
      //     applyId: item.id,
      //     projectId: item.object_id,
      //   },
      // });
    },
  },
};
</script>

