<template>
  <van-pull-refresh
    v-model="refreshing"
    @refresh="onRefresh"
    class="manage-list-wrapper"
  >
    <empty-page emptyMsg="暂无消息" :showEmpty="!list.length" />
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="— 干工程是有底线的 —"
      :immediate-check="false"
      class="list"
      @load="loadMore"
    >
      <template v-for="company in list">
        <div
          v-if="company.is_apply_obj === '1'"
          :key="company.org_id"
          class="company-item"
          @click="toDetail(company)"
        >
          <div>{{ company.org_name }}</div>
          <van-icon name="info-o" class="info-icon" />
        </div>
        <app-fold
          v-else
          ref="company-fold"
          :key="company.org_id"
          :title="company.org_name"
          class="mt10"
          :showTitleLine="true"
          :defaultFold="true"
          @click="isFold => handleClick(isFold, company)"
        >
          <van-loading v-if="!company.proj_list" size="12px">
            加载中...
          </van-loading>
          <Item
            v-else
            :list="company.proj_list"
            :showInfo="type == 'approve'"
            :type="type"
          />
        </app-fold>
      </template>
    </van-list>
  </van-pull-refresh>
</template>

<script>
import AppFold from '@components/app-fold';
import Item from './project-item';
import { PullRefresh } from 'vant';
import { getApplyProjList, getApplyProjListInOrg } from '@src/api/user-state';

const typeMap = {
  auth: { approvalState: 1 },
  approve: { approvalState: 0 },
};
let totalPage = 0;

export default {
  props: {
    type: {
      type: String,
      default: '',
    },
  },
  components: {
    [PullRefresh.name]: PullRefresh,
    AppFold,
    Item,
  },
  data() {
    return {
      currPage: 1,
      loading: false, // 是否正在加载数据
      refreshing: false, // 下拉刷新加载loading显示
      finished: false,
      list: [],
      // projList: {},
    };
  },

  created() {
    this.init();
  },

  activated() {
    this.init();
  },
  methods: {
    init() {
      if (this.loading) {
        return false;
      }

      totalPage = 0;
      this.currPage = 1;
      this.loading = false;
      this.list = [];
      this.load();
    },

    load() {
      if (this.loading) {
        return Promise.reject(false);
      }

      this.loading = true;
      // this.projList = {};

      // 如果有展开的，关闭展开的
      if (this.$refs['company-fold']) {
        this.$refs['company-fold'].map(item => {
          item.setFold(true);
        });
      }

      return getApplyProjList(typeMap[this.type])
        .then(res => {
          totalPage = 1;
          if (this.currPage === 1) {
            this.list = res || [];
          } else {
            this.list = this.list.concat(res || []);
          }
          this.currPage++;
          this.loading = false;
          this.finished = !!(totalPage && this.currPage > totalPage);
          return Promise.resolve();
        })
        .catch(err => {
          console.log('获取失败', err);
          this.loading = false;
          return Promise.reject();
        });
    },

    onRefresh() {
      if (this.loading) {
        return false;
      }
      this.currPage = 1;
      this.refreshing = true;
      this.load()
        .then(() => {
          this.refreshing = false;
        })
        .catch(() => {
          this.refreshing = false;
        });
    },

    loadMore() {
      if (this.finished) {
        return false;
      }
      return this.load();
    },

    openFlow(item) {
      if (item.approval_state !== '1') {
        this.$router.push({
          path: '/user/apply-info',
          query: { applyId: item.apply_id, projectId: item.project_id },
        });
      }
      return false;
    },

    handleClick(isFold, company) {
      // 已授权的按公司拉取项目，审核中的还是全部拉取数据（因为审核中的数据不会太多）
      if (
        this.type === 'auth' &&
        !isFold &&
        (!company.proj_list || !company.proj_list.length)
      ) {
        this.getProjectListInOrg(company);
      }
    },

    // 获取公司层级下的项目列表
    getProjectListInOrg(company) {
      const params = typeMap[this.type];
      params.orgId = company.org_id;
      return getApplyProjListInOrg(params)
        .then(res => {
          this.$set(company, 'proj_list', res.projectList || []);
          // this.$set(this.projList, company.org_id, res.projectList || []);
        })
        .catch(err => {
          this.$set(company, 'proj_list', []);
          console.log('获取失败', err);
        });
    },

    // 公司撤销，撤销接口和项目撤销相同
    toDetail(item) {
      if (this.type !== 'approve') return;
      this.$router.push({
        name: 'applyDetail',
        params: { applyId: item.apply_id, projectId: item.org_id },
      });
    },
  },
};
</script>

